import { useNavigate, useParams } from 'react-router-dom';
import { AppRoot, Title, Text, Button } from '@telegram-apps/telegram-ui';
import { useEffect, useState } from 'react';
import { assets } from './data/assets';
import { TonConnectUI } from '@tonconnect/ui';
import {
  getJettonTransferPayload,
  toNano,
  getJettonWalletAddress,
  getJettonBalance,
} from './jetton-utils';
import { TonClient } from '@ton/ton';
import { Address } from '@ton/core';

const SHOW_DEBUG = true;
const TON_ENDPOINT = 'https://testnet.toncenter.com/api/v2/jsonRPC';

const tonConnect = new TonConnectUI({
  manifestUrl: 'https://merc1305.github.io/build/tonconnect-manifest.json',
});

const client = new TonClient({ endpoint: TON_ENDPOINT });

export const AssetBuy = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const asset = assets.find((a) => a.id === id);

  const minInvest = parseInt((asset?.minInvest ?? '0').replace(/\D/g, ''), 10);
  const [amount, setAmount] = useState(minInvest || 0);
  const [balance, setBalance] = useState<bigint | null>(null);

  useEffect(() => {
    if (!asset || !tonConnect.account) return;

    const fetchBalance = async () => {
      try {
        const account = tonConnect.account;
        if (!account) return;
        const jettonWallet = await getJettonWalletAddress(client, account.address, asset.id);
        const provider = client.provider(Address.parse(jettonWallet));
        const bal = await getJettonBalance(provider);
        setBalance(bal);
      } catch (e) {
        console.error('Ошибка при получении баланса Jetton:', e);
      }
    };

    fetchBalance();
  }, [asset]);

  if (!asset) {
    return (
      <AppRoot>
        <div style={{ padding: 16 }}>
          <Text>Актив не найден</Text>
          <Button onClick={() => navigate(-1)}>← Назад</Button>
        </div>
      </AppRoot>
    );
  }

  const progress = Math.max(1, Math.floor(asset.raised / asset.goal * 100));
  const remaining = asset.goal - asset.raised;

  const quickButtons = [
    minInvest,
    minInvest * 2,
    Math.floor(remaining / 2 / minInvest) * minInvest,
    Math.floor(remaining / minInvest) * minInvest,
  ].filter((value, index, arr) => arr.indexOf(value) === index && value <= remaining);

  const handleBuy = async () => {
    try {
      if (!tonConnect.account) {
        await tonConnect.connectWallet();
      }

      const wallet = tonConnect.account;
      if (!wallet) {
        alert('Кошелёк не подключён');
        return;
      }

      const amountNano = toNano(amount);
      const jettonWalletAddress = await getJettonWalletAddress(client, wallet.address, asset.id);
      const provider = client.provider(Address.parse(jettonWalletAddress));
      console.log('Jetton wallet address:', jettonWalletAddress);
      console.log('User wallet address:', wallet.address);
      console.log('Jetton master address (asset.id):', asset.id);
      const balance = await getJettonBalance(provider);

      if (balance < BigInt(amountNano)) {
        alert(`Недостаточно USDT. Доступно: ${(Number(balance) / 1e9).toFixed(2)} USDT`);
        return;
      }

      const tx = {
        validUntil: Math.floor(Date.now() / 1000) + 60,
        messages: [
          {
            address: wallet.address,
            amount: '0',
            payload: getJettonTransferPayload(asset.id, amountNano),
          },
        ],
      };

      await tonConnect.sendTransaction(tx);
      alert(`Успешно отправлено ${amount} USDT`);
    } catch (err) {
      console.error(err);
      alert(`Ошибка при отправке Jetton USDT - ${err}`);
    }
  };

  return (
    <AppRoot>
      <div
        style={{
          minHeight: '100vh',
          display: 'flex',
          flexDirection: 'column',
          justifyContent: 'space-between',
          padding: 16,
          backgroundColor: 'var(--tg-theme-secondary-bg-color, #f9f9f9)',
        }}
      >
        <div style={{ maxWidth: 600, width: '100%', margin: '0 auto' }}>
          {tonConnect.account && SHOW_DEBUG && (
            <div style={{ display: 'flex', flexDirection: 'column', marginBottom: 12 }}>
              <Text
                style={{
                  fontSize: 14,
                  color: 'var(--tg-theme-hint-color, #888)',
                  wordBreak: 'break-word',
                  whiteSpace: 'pre-wrap',
                  marginBottom: 4,
                }}
              >
                Адрес:
                {'\n'}
                {tonConnect.account.address}
              </Text>
              <Text
                style={{
                  fontSize: 14,
                  color: 'var(--tg-theme-hint-color, #888)',
                  marginBottom: 4,
                }}
              >
                Баланс: {balance !== null ? (Number(balance) / 1e9).toFixed(2) : '...'} USDT
              </Text>
              <Text
                style={{
                  fontSize: 14,
                  color: 'var(--tg-theme-hint-color, #888)',
                  marginBottom: 8,
                }}
              >
                Сеть: {TON_ENDPOINT.includes('testnet') ? 'testnet' : 'mainnet'}
              </Text>
              <Button
                onClick={() => tonConnect.disconnect()}
                size="s"
                style={{
                  alignSelf: 'flex-start',
                  backgroundColor: '#eee',
                  color: '#000',
                  marginBottom: 8,
                }}
              >
                Отключить кошелёк
              </Button>
            </div>
          )}

          <Button
            onClick={() => navigate(-1)}
            size="l"
            style={{
              marginBottom: 16,
              backgroundColor: 'var(--tg-theme-bg-color, #eee)',
              color: 'var(--tg-theme-text-color, #000)',
            }}
          >
            ← Назад
          </Button>

          <div
            style={{
              padding: 16,
              backgroundColor: 'var(--tg-theme-bg-color, #fff)',
              borderRadius: 16,
              marginBottom: 24,
            }}
          >
            <Title level="2" style={{ marginBottom: 4, color: 'var(--tg-theme-text-color, #000)' }}>
              {asset.name}
            </Title>
            <Text style={{ color: 'var(--tg-theme-hint-color, #666)', marginBottom: 8 }}>
              От {asset.minInvest} • Доходность {asset.returnRate}
            </Text>

            <div
              style={{
                height: 6,
                backgroundColor: 'var(--tg-theme-secondary-bg-color, #eee)',
                borderRadius: 4,
                overflow: 'hidden',
                marginBottom: 8,
              }}
            >
              <div
                style={{
                  height: '100%',
                  width: `${progress}%`,
                  backgroundColor: 'var(--tg-theme-link-color, #3390ec)',
                }}
              />
            </div>

            <Text style={{ fontSize: 14, color: 'var(--tg-theme-text-color, #000)' }}>
              {asset.raised.toLocaleString()} USDT ({progress}%) из{' '}
              {asset.goal.toLocaleString()} USDT
            </Text>

            <input
              type="number"
              step={minInvest}
              min={minInvest}
              max={remaining}
              value={amount}
              onChange={(e) => {
                const val = parseInt(e.target.value);
                if (!isNaN(val)) {
                  const nearest = Math.max(minInvest, Math.floor(val / minInvest) * minInvest);
                  const corrected = Math.min(nearest, remaining);
                  setAmount(corrected);
                }
              }}
              style={{
                width: '100%',
                padding: '12px 16px',
                fontSize: 16,
                borderRadius: 12,
                border: '1px solid #ccc',
                marginBottom: 16,
                boxSizing: 'border-box',
              }}
            />

            <div
              style={{
                display: 'flex',
                flexWrap: 'wrap',
                gap: 8,
                justifyContent: 'space-between',
                marginBottom: 16,
              }}
            >
              {quickButtons.map((value) => (
                <Button
                  key={value}
                  style={{
                    flex: '1 1 45%',
                    backgroundColor: 'var(--tg-theme-secondary-bg-color, #eee)',
                    color: 'var(--tg-theme-text-color, #000)',
                  }}
                  onClick={() => setAmount(value)}
                >
                  {value.toLocaleString()}
                </Button>
              ))}
            </div>
          </div>
        </div>

        <div style={{ maxWidth: 600, width: '100%', margin: '0 auto' }}>
          <Button
            size="l"
            style={{ width: '100%', marginTop: 16, marginBottom: 24 }}
            onClick={handleBuy}
          >
            Купить на {amount.toLocaleString()} USDT
          </Button>
        </div>
      </div>
    </AppRoot>
  );
};
