import { useNavigate } from 'react-router-dom';
import { AppRoot, Title, Text, Button, Card } from '@telegram-apps/telegram-ui';

export const Profile = () => {
  const navigate = useNavigate();
  const user = typeof window !== 'undefined' && window.Telegram?.WebApp?.initDataUnsafe?.user;

  return (
    <AppRoot>
      <div
        style={{
          display: 'flex',
          justifyContent: 'center',
          backgroundColor: 'var(--tg-theme-secondary-bg-color, #f9f9f9)',
          minHeight: '100vh',
          padding: 16,
        }}
      >
        <div style={{ width: '100%', maxWidth: 600 }}>
          {/* Назад */}
          <Button
            onClick={() => navigate(-1)}
            size="l"
            style={{
              marginBottom: 16,
              backgroundColor: 'var(--tg-theme-bg-color, #eee)',
              color: 'var(--tg-theme-text-color, #000)',
            }}
          >
            ← Назад
          </Button>

          {/* Имя и фамилия */}
          <Title level="2" style={{ color: 'var(--tg-theme-text-color, #000)', marginBottom: 16, textAlign: 'center' }}>
            {user ? `${user.first_name} ${user.last_name ?? ''}` : 'Профиль'}
          </Title>

          {/* Таблица */}
          <Card
            style={{
              padding: 16,
              marginBottom: 24,
              marginRight: 8,
              width: '90%',
              maxWidth: 570,
              backgroundColor: 'var(--tg-theme-bg-color, #fff)',
              borderRadius: 16,
            }}
          >
            <Row label="Стоимость активов:" value="0 USDT" />
            <Row label="Месячный доход:" value="0 USDT" last />
          </Card>

          {/* Кнопка подключения */}
          <Button
            size="l"
            style={{ width: '100%', marginBottom: 32 }}
            onClick={() => console.log('Подключить кошелёк')}
          >
            Подключить кошелёк
          </Button>
        </div>
      </div>
    </AppRoot>
  );
};

const Row = ({
  label,
  value,
  hint,
  last = false,
}: {
  label: string;
  value: string | number;
  hint?: string;
  last?: boolean;
}) => (
  <div
    style={{
      padding: '16px 0',
      borderBottom: last ? 'none' : '1px solid var(--tg-theme-secondary-bg-color, #eee)',
      display: 'flex',
      justifyContent: 'space-between',
      alignItems: hint ? 'flex-start' : 'center',
      gap: 16,
    }}
  >
    <div style={{ flex: 1 }}>
      <Text
        style={{
          color: 'var(--tg-theme-text-color, #000)',
          fontWeight: 600,
          fontSize: 16,
          lineHeight: '20px',
        }}
      >
        {label}
      </Text>
      {hint && (
        <Text
          style={{
            color: 'var(--tg-theme-hint-color, #888)',
            fontSize: 13,
            lineHeight: '18px',
            marginTop: 4,
          }}
        >
          {hint}
        </Text>
      )}
    </div>

    <div
      style={{
        textAlign: 'right',
        color: 'var(--tg-theme-text-color, #000)',
        fontWeight: 600,
        fontSize: 16,
        lineHeight: '20px',
        minWidth: 100,
        display: 'flex',
        marginRight: 10,
        alignItems: 'center',
      }}
    >
      {value}
    </div>
  </div>
);
